.contact-page {
  margin-left: 10%;
}

.contact-form {
  width: 100%;
  // height: 100%;
  margin-top: 20px;

  // background: url(https://i.ibb.co/jyRLrBZ/world-map.png);

  ul {
    padding: 0;
    margin: 0;
    margin-top: -74px;
    li {
      padding: 0;
      margin: 0;
      border-radius: 0.5rem;
      list-style: none;
      margin-bottom: 10px;
      opacity: 0;
      overflow: hidden;
      display: block;
      clear: both;
      position: relative;
      animation: fadeInUp 2s 2s;
      animation-fill-mode: forwards;
    }

    li.half {
      width: 49%;
      margin-left: 2%;
      float: left;
      clear: none;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  input[type='text'],
  input[type='email'] {
    width: 100%;
    border: 0;
    background: #35353576;
    height: 50px;
    font-size: 16px;
    color: #fff;
    padding: 0 20px;
    box-sizing: border-box;
    
      &:focus {
        box-shadow: none;
        border-bottom: 2px solid #08fdd8;
        transition: 0.3s ease all;
        top: 0;
        opacity: 1;
        color: #8e44ad;
        font-size: 15px;

        &:after {
          visibility: visible;
          width: 100%;
          left: 0;

        }
      }
    
  }

  textarea {
    width: 100%;
    border: 0;
    background: #35353576;
    height: 50px;
    font-size: 16px;
    color: #fff;
    padding: 20px;
    box-sizing: border-box;
    min-height: 150px;

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 45%;
      height: 2px;
      width: 10px;
      visibility: hidden;
    }

    &:focus {
      box-shadow: none;
      border-bottom: 2px solid #08fdd8;
      transition: 0.2s ease all;
      top: 0;
      opacity: 1;
      color: #8e44ad;
      font-size: 15px;

      &:after {
        visibility: visible;
        width: 100%;
        left: 0;

      }
    }
  }
}

.map-wrap {
