.leaflet-container {
  position: relative;
  width: 100%;
  height: 100%;
  opacity: 0;
  animation: backInRight 1s 1.2s;
  animation-fill-mode: forwards;
  margin-left: 14%;
  left: 18%
}

.info-map {
  position: absolute;
  background: #fff;
  top: 50px;
  z-index: 1;
  width: 267px;
  padding: 20px;
  color: #fff;
  font-family: 'Helvetica';
  font-size: 17px;
  font-weight: 300;
  opacity: 0;
  border-radius: 0.5rem;
  animation: fadeIn 1s 1.5s;
  animation-fill-mode: forwards;

  span {
    font-size: 16px;

    span {
      color: #ffd700;
    }
  }
}

.flat-button1 {
  margin: 20px;
  outline: none;
  width: 130px;
  height: 40px;
  border: 2px solid #fff;
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  
}


.flat-button1 {
  color: #08fdd8;
  font-size: 13px;
  letter-spacing: 4px;
  font-family: "Open Sans", sans-serif;
  text-decoration: none;
  padding: 10px 18px;
  border: 1px solid #08fdd8;
  margin-top: 25px;
  float: left;
  white-space: nowrap;



  background: {
    image: linear-gradient(95deg, #08fdd8 50%, transparent 50%);
    position: 100%;
    size: 400%;
  }


  &:hover {
    background-position: 0;
    border: 2px solid #08fdd8;
    color: #333;
    transition: background .3s ease-in-out;
  }


  &:focus {
    // color: #fff;
    color: #333;

    outline: 0;
  }

  @keyframes fadeInUp {
    0% {
      opacity: 0;
      -webkit-transform: translate3d(0, 100%, 0);
      -ms-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0);
    }

    100% {
      opacity: 1;
      -webkit-transform: none;
      -ms-transform: none;
      transform: none;
    }
  }
}
